<script setup>
import {
  ref,
  reactive,
  onBeforeMount,
  watchEffect,
  computed,
  watch,
} from "vue";
import assetsBanner from "./components/assetsBanner.vue";
import income from "./components/income.vue";
import incomeRecord from "./components/incomeRecord.vue";
import inviteRecord from "./components/inviteRecord.vue";
import inviteBox from "./components/inviteBox.vue";
import bindParent from "./components/bindParent.vue";
import copyLink from "./components/copyLink.vue";
import { commonStore } from "@/store/common";
import { swapStore } from "@/store/swap";
import { Toast } from "vant";
import { getProfitList } from "@/http/api";
import { balanceUtil } from "@/utils";
import { useI18n } from "vue-i18n";

const common_store = commonStore();
const swap_store = swapStore();
const { t } = useI18n();
const userInfo = computed(() => common_store.userInfo);
const balance = computed(() => swap_store.balacne);
const data = reactive({
  show: false,
  name: "",
  list: [],
  loading: true,
  finished: false,
  page: 0,
  bindIng: false,
  recordShow: false,
  total: 0,
});
const navArr = ["Account.nav.one", "Account.nav.two", "time"];
const showPopup = (name) => {
  data.name = name;
  data.show = !data.show;
};
const bindFn = async (address) => {
  data.bindIng = true;
  try {
    const res = await common_store.marketMethods
      .bind(address)
      .send({ from: common_store.address });
    sessionStorage.removeItem("parent");
    Toast(t("Account.bindSuccess"));
  } catch (error) {
    Toast(error);
  }
  common_store.getUserInfo();
  data.show = false;
  data.bindIng = false;
};
const getList = () => {
  if (data.finished) return;
  getProfitList(common_store.address, data.page).then((res) => {
    data.loading = false;
    const list = res.data.results;
    data.list = data.list.concat(list);
    data.finished = list.length < 20 || res.data.length == 0;
  });
};
const onLoad = () => {
  if (data.finished) {
    return;
  }
  data.page++;
  data.loading = true;
  getList();
};
onBeforeMount(async () => {
  if (!swap_store.tokenEmpower) {
    await swap_store.getTokenMethods();
  }
  if (common_store.address) {
    getList();
  }
});
watch(
  () => common_store.address,
  () => {
    data.page = 0;
    data.list = [];
    data.loading = true;
    data.finished = false;
    getList();
  }
);
watchEffect(() => {
  if (common_store.address) {
    swap_store.getBalance();
    common_store.initMarketWeb3();
  }
});
</script>
<template>
  <div class="account">
    <p class="account-title Wcolor font-Tt Bold">{{ $t("Account.title") }}</p>
    <div class="account-top accountBg allFlex column">
      <assets-banner :assets="balance"></assets-banner>
      <income
        :total="balanceUtil(userInfo.userinfo_.nftprofit)"
        :income="balanceUtil(userInfo.userinfo_.extprofit)"
      ></income>
    </div>

    <invite-box
      :total="userInfo.sons_.length"
      @showPopup="showPopup"
      :parent="userInfo.userinfo_.superadd"
    ></invite-box>

    <!-- 收益记录 -->
    <div
      class="income-record titleBg"
      :style="{ height: data.recordShow ? '455px' : '70px' }"
    >
      <div
        class="flex_between record-title padding bg"
        @click="data.recordShow = !data.recordShow"
      >
        <div class="flex_start">
          <img
            src="@assets/accountImg/record1.png"
            alt=""
            class="record_icon"
          />
          <span class="Wcolor Bold marginTen">{{ $t("Account.record1") }}</span>
        </div>
        <img
          src="@assets/accountImg/down_icon.png"
          alt=""
          class="down_icon"
          :class="[data.recordShow ? 'up_icon' : 'down_icon']"
        />
      </div>
      <div class="record-bottom">
        <record-nav
          oneWidth="85px"
          endWidth="128px"
          :navArr="navArr"
        ></record-nav>
        <van-list
          v-model:loading="data.loading"
          :finished="data.finished"
          :immediate-check="false"
          @load="onLoad"
          :loading-text="$t('loadingText')"
        >
          <div class="income-record_main">
            <income-record :list="data.list"></income-record>
          </div>
        </van-list>
      </div>
    </div>
    <!-- 邀请记录 -->
    <invite-record
      :timeArr="userInfo.userinfo_.sonregtime"
      :sons="userInfo.sons_"
    ></invite-record>

    <van-popup v-model:show="data.show">
      <bind-parent
        @closeFn="data.show = !data.show"
        @bindParent="bindFn"
        :check="data.bindIng"
        v-show="data.name == 'bind'"
      >
      </bind-parent>
      <!-- <copy-link
        @closeFn="data.show = !data.show"
        v-show="data.name == 'invite'"
      ></copy-link> -->
    </van-popup>
  </div>
</template>

