  padding: 75px 15px 70px 15px;
  background: url("../../assets/accountImg/bg.png") no-repeat;
  background-size: 100% auto;
  &-top {
    height: 190px;
    box-shadow: 0px 2px 10px rgba(13, 32, 77, 0.18);
    border: 1px solid $border2;
    margin: 0 auto;
    padding: 10px 15px;
    border-radius: 10px;
  }
  &-title {
    height: 80px;
    line-height: 80px;
  }
}
.income-record {
  margin: 20px 0;
  border-radius: 12px;
  transition: ease-in-out 0.2s;
  overflow: hidden;
  &_main {
    max-height: 350px;
    overflow-y: auto;
  }
}
.record-title {
  height: 70px;
}
.record_icon,
.down_icon {
  width: 25px;
  height: 25px;
}
.down_icon {
  transition: ease-in-out 0.2s;
  transform: rotate(180deg);
}
.up_icon {
  transition: ease-in-out 0.2s;
  transform: rotate(0);
}
.record-bottom {
  padding: 0 8px;
  border-top: 2px solid $border2;
}
</style>
