<script setup>
import { ref, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import { Toast } from "vant";
import { useI18n } from "vue-i18n";

const router = useRouter();
const { t } = useI18n();
const props = defineProps({
  total: Number,
  parent: String,
});
const { parent } = toRefs(props);
defineEmits(["showPopup"]);
const toInvite = () => {
  if (parent.value.indexOf("0x00000000") >= 0) {
    Toast(t("Account.parentAlert"));
    return;
  }
  router.push("/invite");
};
</script>
<template>
  <ul class="flex_between income">
    <li class="accountBg flex_start">
      <!-- <span class="greyColor font-One">{{ $t('Account.parent') }}</span> -->
      <p
        class="Wcolor font-Four Bold"
        v-if="parent && parent.indexOf('0x00000') < 0"
      >
        {{ parent.slice(0, 6) }}...{{ parent.slice(-4) }}
      </p>
      <p
        v-else
        class="orangeColor font-Four"
        @click="$emit('showPopup', 'bind')"
      >
        {{ $t("Account.bindBtn") }}
      </p>
    </li>
    <li class="flex_between accountBg">
      <div>
        <span
          class="priceColor font-One"
          :class="[$i18n.locale == 'EN' && 'en_text']"
          >{{ $t("Account.invitePerson") }}</span
        >
        <p class="Wcolor font-Six">{{ total }}</p>
      </div>

      <van-button size="mini" type="primary" @click="toInvite">{{
        $t("Account.invite")
      }}</van-button>
    </li>
  </ul>
</template>

<style lang="scss" scoped>
.income {
  margin: 15px 0;
  position: relative;
  p {
    line-height: 30px;
  }
  li {
    width: 48%;
    height: 60px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid $border2;
  }
}
.right {
  text-align: right;
}
</style>
